@keyframes swell {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(0.3); }
  100% {
    transform: scale(1); } }

.robot-chat-loading i {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 6px;
  background-color: #e0f1fc;
  position: relative;
  top: 1px;
  animation: swell 1s linear infinite; }
  .robot-chat-loading i + i {
    margin-left: 6px; }
  .robot-chat-loading i:nth-child(1) {
    animation-delay: 0; }
  .robot-chat-loading i:nth-child(2) {
    animation-delay: .2s; }
  .robot-chat-loading i:nth-child(3) {
    animation-delay: .4s; }
  .robot-chat-loading i:nth-child(4) {
    animation-delay: .6s; }
  .robot-chat-loading i:nth-child(5) {
    animation-delay: .8s; }
