.contact-page .contact-form {
  margin-top: 20px;
  overflow: hidden; }
  @media screen and (max-width: 750px) {
    .contact-page .contact-form {
      margin-top: 0; } }
  .contact-page .contact-form-username, .contact-page .contact-form-textarea {
    margin-bottom: 15px; }
  .contact-page .contact-form button {
    padding: 10px 32px;
    float: right; }
    @media screen and (max-width: 750px) {
      .contact-page .contact-form button {
        width: 100%; } }
