.robot-page {
  height: 100vh;
  position: relative; }
  .robot-page .robot-panel {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    background-color: #fff;
    width: 375px;
    height: 700px;
    box-shadow: 0 3px 20px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    overflow: hidden; }
    @media screen and (max-width: 765px) {
      .robot-page .robot-panel {
        height: 100vh;
        width: 100vw;
        border-radius: 0; } }
